#topbarContainer {
  background-color: #514e57;
  padding-left: 15px;
  height: 50px;
}

a {
  white-space: nowrap;
  cursor: default;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: white;
  font-size: 12pt;
  font-weight: bold;
  text-decoration: none;
  background: transparent;
  border-style: none;
  vertical-align: center;
  padding: 10px;
  background: transparent;
  border: none;
}

#logo {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 18pt;
  font-style: italic;
}

a:hover {
  background-color: #898a83;
  border-radius: 5px;
  cursor: pointer;
}

#filler {
  width: 100%;
}
